.board-row {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: #0B0B0B;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
}

.board-container {
  margin: 20px;
  height: 500px;
  width: 500px;
  align-items: center;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  flex-direction: column; /* Align children vertically */
  justify-content: center; /* Align children horizontally */
  text-align: center; /* Center the button horizontally */
}

.square {
  width: 33.33%;
  height: 33.33%;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 5px solid #F5C518;
  cursor: pointer;
  font-size: 100px;
  color: red;
  background-color: #1E1E1E;
  transition: background-color 0.3s, color 0.3s;
  text-align: center;
  line-height: 100px;
}

.square:hover {
  background-color: #F5C518;
  color: #333;
}

h2, h4 {
  margin: 10px 0;
  color: transparent; /* Set text color to transparent */
  text-align: center;
  font-family: 'Arcade'; /* Use a playful and attractive font */
  background: linear-gradient(45deg, #FFD700, #FF5733, #C70039); /* Apply gradient background */
  -webkit-background-clip: text; /* Clip the background to the text */
  -webkit-text-fill-color: transparent; /* Set text color to transparent */
  animation: shadowAnimation 2s infinite alternate, gradientAnimation 10s ease infinite; /* Add animations */
}
h1 {
  font-size: 72pt;
  margin: 10px 0;
  color: transparent; /* Set text color to transparent */
  text-align: center;
  font-family: 'Arcade'; /* Use a playful and attractive font */
  background: linear-gradient(45deg, #FFD700, #FF5733, #C70039); /* Apply gradient background */
  -webkit-background-clip: text; /* Clip the background to the text */
  -webkit-text-fill-color: transparent; /* Set text color to transparent */
  animation: shadowAnimation 2s infinite alternate, gradientAnimation 10s ease infinite; /* Add animations */
}
@keyframes shadowAnimation {
  0% {
      text-shadow: 0 0 10px red;
  }
  100% {
      text-shadow: 0 0 20px red, 0 0 30px #C70039, 0 0 40px #FFD700;
  }
}

@keyframes gradientAnimation {
  0% {
      background-position: 0% 50%;
  }
  50% {
      background-position: 100% 50%;
  }
  100% {
      background-position: 0% 50%;
  }
}

h2 {
  font-size: 48px;
}

h4 {
  font-size: 32px;
}


button {
  margin-top: 20px;
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  border: none;
  background-color: #F5C518;
  color: #333;
  border-radius: 5px;
  transition: background-color 0.3s, color 0.3s;
 
}

button:hover {
  background-color: #333;
  color: #F5C518;
}
